import '@servicenow/now-icon';
import {FOCUS_FIRST_NODE, NOW_PANEL_OPENED_SET} from '../constants';
import {actionTypes, createCustomElement} from '@servicenow/ui-core';
import {findFirstTabbableElement} from '../utils/focusUtility';
import {t} from 'sn-translate';
import keyBindingBehavior from '@servicenow/behavior-key-binding';
import packageJson from '../../package.json';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './nowRecordListPanel.scss';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;

const hidePanel = (e, dispatch) => {
	e.preventDefault();
	dispatch(NOW_PANEL_OPENED_SET, {
		panelOpened: false,
		panelConfig: {}
	});
};

/**
 * Closes the panel when the user presses the Escape key.
 */
const escapePanelHandler = (host, evt, dispatch) => {
	hidePanel(evt, dispatch);
};

const focusFirstNodeHandler = coeffects => {
	const {host} = coeffects;
	const slotElementToFocus = host.shadowRoot.querySelector('#panelSlot');
	const defaultElementToFocus = host.shadowRoot.querySelector('#panel');

	const firstTabbableElement = findFirstTabbableElement(slotElementToFocus);

	if (firstTabbableElement) {
		firstTabbableElement.focus();
	} else {
		const defaultTabbableElement = findFirstTabbableElement(
			defaultElementToFocus
		);
		if (defaultTabbableElement) {
			defaultTabbableElement.focus();
		}
	}
};

const view = (state, {dispatch}) => {
	const {
		properties: {
			content,
			panelOpened,
			panelTitle,
			panelSize,
			hidePadding,
			renderPanelHeader
		}
	} = state;
	const CLOSE = t('Close');

	return (
		<div
			hidden={!panelOpened}
			role="complementary"
			aria-labelledby="panelHeaderTitle">
			<div
				id="panel"
				class={{'now-record-list-panel': true, ['-' + panelSize]: panelSize}}>
				{!renderPanelHeader ? null : (
					<div className="now-record-list-panel-header">
						<div className="now-record-list-panel-header-content">
							<h2
								id="panelHeaderTitle"
								className="now-record-list-panel-header-title">
								{panelTitle}
							</h2>
						</div>
						<div className="now-record-list-panel-header-content">
							<now-button-iconic
								className="now-record-list-panel-close-btn"
								configAria={{
									'aria-label': CLOSE,
									'aria-expanded': 'true'
								}}
								icon="close-outline"
								variant="tertiary"
								size="md"
								bare
								on-click={event => {
									hidePanel(event, dispatch);
								}}
							/>
						</div>
					</div>
				)}
				<div
					className={`now-record-list-panel-body ${
						hidePadding ? '-no-padding' : ''
					}`}>
					<slot id="panelSlot">
						<p>
							<span className="now-line-height-crop">{content}</span>
						</p>
					</slot>
				</div>
			</div>
		</div>
	);
};

createCustomElement('now-record-list-panel', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * Sets the size of the panel to one of three preset dimensions.
		 * @type {('sm'|'md'|'lg')}
		 */
		panelSize: {default: 'sm'},
		/**
		 * Indicates if the panel is currently opened. Use `manage-opened` to override
		 * the default behavior and handle the `NOW_PANEL#OPENED_SET` action manually.
		 * @type {boolean}
		 */
		panelOpened: {
			default: false,
			manageable: true
		},
		/**
		 * Indicated if the panel will render the text in panelTitle as a header.
		 * @type {string}
		 */
		renderPanelHeader: {default: true},
		/**
		 * The text content for the panel header.
		 * @type {string}
		 */
		panelTitle: {default: ''},
		/**
		 * The text content for the panel. Use the "content" slot to display
		 * non-text content in the body of the panel.
		 * @type {string}
		 */
		content: {},
		/**
		 * Indiciates if the panel body should show padding.
		 * @type {boolean}
		 */
		hidePadding: {default: false}
	},
	styles,
	behaviors: [
		{
			behavior: keyBindingBehavior,
			options: {
				keyBindings: {
					onEscapePress: ['Escape']
				},
				handlers: {
					onEscapePress: escapePanelHandler
				}
			}
		}
	],
	dispatches: {
		/**
		 * Dispatched when the panel is dismissed by the user clicking the dismiss
		 * button or pressing the `Escape` key while the panel is opened. Set the
		 * `manage-opened` property to override the default behavior and
		 * handle this action manually.
		 * @type {{value: boolean}}
		 */
		'NOW_PANEL#OPENED_SET': {}
	},
	actionHandlers: {
		[FOCUS_FIRST_NODE]: {
			effect: focusFirstNodeHandler
		},
		[COMPONENT_BOOTSTRAPPED]: focusFirstNodeHandler
	},
	onConnect(host) {
		if (!window.listComponentRegistry) {
			window.listComponentRegistry = new WeakMap();
		}
		window.listComponentRegistry.set(host, packageJson.version);
	},
	onDisconnect(host) {
		if (window.listComponentRegistry) {
			window.listComponentRegistry.delete(host);
		}
	}
});
