// -----------------------------------------------------------------------------
// Constants
// -----------------------------------------------------------------------------

// Selectors to get common tabbable elements.
const selector = [
	'input',
	'select',
	'textarea',
	'a[href]',
	'button',
	'[tabindex]',
	'audio[controls]',
	'video[controls]',
	'[component-id]',
	'[contenteditable]:not([contenteditable="false"])'
].join();

export const UPDATE_FOCUS_TRAP = 'UPDATE_FOCUS_TRAP';

// -----------------------------------------------------------------------------
// Shadow Dom Focus Utilities
// -----------------------------------------------------------------------------

export const findFirstTabbableElement = (root = document) => {
	/* findFirstTabbableElement
	 * Parameters:
	 *  root:
	 *    Expected Type: Node, Document, ShadowRoot
	 *    Optional: Defaults to document.
	 * Return Type: Node
	 * Description:
	 *  Finds the first tabbable element in an element or component.
	 */
	return findTabbableElement(true, root);
};

export const findTabbableElement = (findFirst = true, root = document) => {
	// Return Type: Node
	// Finds the first or last tabbable element in an element or component.
	if (!root) return false;

	if (root.shadowRoot) {
		root = root.shadowRoot;
	} else if (root.assignedElements) {
		// For slotted content just find the first element to focus
		root = root.assignedElements()[0];
	}

	let tabbies = [...root.querySelectorAll(selector)];
	if (!findFirst) tabbies = tabbies.reverse();

	let index = 0;
	let result = undefined;

	// TODO: Maybe change code style here
	while (!result && index < tabbies.length) {
		const tabby = tabbies[index++];
		const possibleResult = tabby.shadowRoot
			? findTabbableElement(findFirst, tabby.shadowRoot)
			: tabby;
		if (possibleResult && isTabbable(possibleResult)) result = possibleResult;
	}
	return result;
};

export const isTabbable = node => {
	// Return Type: Boolean
	// Determines if an element is tabbable based on criteria that can't be done
	// with QuerySelectorAll.

	if (!node) return false;

	if (node.disabled) return false;

	if (node.offsetParent === null) return false;

	if (getComputedStyle(node).visibility === 'hidden') return false;

	if (node.tagName === 'INPUT' && node.type === 'hidden') return false;

	if (getTabIndex(node) === -1) return false;

	return true;
};

export const getTabIndex = node => {
	// Return Type: Number
	// Gets the numeric tab index of an element.

	const tabIndex = parseInt(node.getAttribute('tabindex'), 10);
	if (!isNaN(tabIndex)) return tabIndex;
	return node.contentEditable === 'true' ? 0 : node.tabIndex;
};
